import React, { Component } from 'react';
// import withRouter from 'umi/withRouter';
import echarts from 'echarts';
import jsonData from '../../../data/school/学校视角--基本情况--学校面积.json';

let myChart;
class BasicSituationSchoolArea extends Component {
    state = {
        chartData: [],
    }
    componentDidMount() {
        myChart = echarts.init(document.getElementById('ZGDBasicSituationSchoolAreaChart'));
        this.setState({
            chartData: jsonData.RECORDS
        },()=>{
            this.setChart();
        })
    }
    setChart = () => {
        let data = this.state.chartData || [];
        if (!data || data.length <= 0) {
            return;
        }
        let sjzdData = [], sjjxyfData = [], sjssData =[];
        data.forEach(item=>{
            sjzdData.push(item.landUseEqually);
            sjjxyfData.push(item.teachLandEqually);
            sjssData.push(item.hostelLandEqually);
        });
        let xAxisData = ["教育部标准","本校","标杆院校"];
        
        let option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    lineStyle: {
                        color: '#57617B'
                    }
                }
            },
            legend: {
                icon: 'circle',
                itemWidth: 14,
                itemHeight: 5,
                itemGap: 13,
                bottom: '4%',
                textStyle: {
                    fontSize: 12,
                    color: '#00cfff'
                }
            },
            grid: {
                top: '10%',
                left: '5%',
                right: '8%',
                bottom: '15%',
                containLabel: true
            },
            xAxis: [{
                type: 'category',
                boundaryGap: false,
                axisLine: {
                    lineStyle: {
                        color: 'rgba(255,255,255,0.49)',
                        width:0,
                    }
                },
                axisTick: {
                    show: false
                },
                splitLine: {
                    show: true,
                    lineStyle: {
                        color: '#8b572a',
                        type: 'dashed',
                    }
                },
                axisLabel: {
                    textStyle: {
                        fontSize: 14,
                        color: "#44ffff"
                    }
                },
                data: xAxisData
            }],
            yAxis: [{
                type: 'value',
                axisTick: {
                    lineStyle: {
                        color: '#8b572a',
                    }
                },
                axisLine: {
                    show: false,
                    lineStyle: {
                        color: 'rgba(255,255,255,0.49)'
                    }
                },
                axisLabel: {
                    margin: 10,
                    textStyle: {
                        fontSize: 14,
                        color: "#44ffff"
                    }
                },
                splitLine: {
                    show: false
                }
            }],
            series: [{
                name: '生均占地面积',
                type: 'line',
                smooth: true,
                lineStyle: {
                    normal: {
                        width: 1
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgba(250, 217, 97, 0.5)'
                        }, {
                            offset: 1,
                            color: 'rgba(250, 217, 97, 0)'
                        }], false),
                        shadowColor: 'rgba(0, 0, 0, 0.1)',
                        shadowBlur: 20
                    }
                },
                itemStyle: {
                    normal: {
                        color: 'rgb(250, 217, 97)'
                    }
                },
                data: sjzdData
            }, {
                name: '生均教学用房面积',
                type: 'line',
                smooth: true,
                lineStyle: {
                    normal: {
                        width: 1
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgba(244, 98, 98, 0.5)'
                        }, {
                            offset: 1,
                            color: 'rgba(244, 98, 98, 0)'
                        }], false),
                        shadowColor: 'rgba(0, 0, 0, 0.1)',
                        shadowBlur: 20
                    }
                },
                itemStyle: {
                    normal: {
                        color: 'rgb(244, 98, 98)'
                    }
                },
                data: sjjxyfData
            }, {
                name: '生均宿舍面积',
                type: 'line',
                smooth: true,
                lineStyle: {
                    normal: {
                        width: 1
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgba(0, 207, 255, 0.5)'
                        }, {
                            offset: 1,
                            color: 'rgba(0, 207, 255, 0)'
                        }], false),
                        shadowColor: 'rgba(0, 0, 0, 0.1)',
                        shadowBlur: 20
                    }
                },
                itemStyle: {
                    normal: {
                        color: 'rgb(0, 207, 255)'
                    }
                },
                data: sjssData
            }, ]
        };

        myChart.setOption(option);
    }
    render() {

        return (
            <div id="ZGDBasicSituationSchoolAreaChart" style={{ width: '100%', height: '100%' }}></div>
        )
    }
}

export default BasicSituationSchoolArea;