import React, { PureComponent } from 'react';
import { Row, Col } from 'antd';
import echarts from 'echarts';
import ageJson from '../../../data/school/学院视角--管理队伍情况--年龄.json';
import xueliJson from '../../../data/school/学院视角--管理队伍情况--学历.json';
import sexJson from '../../../data/school/学院视角--管理队伍情况--性别.json';
import zhichengJson from '../../../data/school/学院视角--管理队伍情况--职称.json';

// 管理队伍情况全部无数据
class ItemComp extends PureComponent {
    componentDidMount() {
        let myChart = echarts.init(document.getElementById(this.props.id));
        let data = this.props.data || [];
        // let total = data.map(item=>item.value).reduce((a,b)=>a+b);
        let xAxisData = [], seriesData, obj = {};
        data.forEach(item => {
            xAxisData.push(item.name);
            Object.keys(item).filter(a=>a!=="name").forEach(b=>{
                if(obj[b]){
                    obj[b].push(item[b]);
                } else {
                    obj[b] = [item[b]]
                }
            })
        });
        let legendData = Object.keys(obj);
        seriesData = legendData.map(item=>{
            return {
                name:item,
                type:this.props.type || 'line',
                stack: this.props.type==="bar"?"a":undefined,
                barWidth: "20px",
                avoidLabelOverlap: false,
                label: {
                    normal: {
                        show: false,
                        position: 'center'
                    },
                    emphasis: {
                        show: true,
                        textStyle: {
                            fontSize: '30',
                            fontWeight: 'bold'
                        }
                    }
                },
                labelLine: {
                    normal: {
                        show: false
                    }
                },
                data:obj[item]
            }
        })
        let option = {
            color: ["#3DB1FF","#BD10E0","#FADB14","#D89C5D","#FF2C2C","#65D62D"],
            legend: {
                data: legendData,
                bottom: 5,
                icon: "circle",
                itemWidth: 5,
                itemHeight: 5,
                textStyle:{
                    color:"#00CFFF",
                }
            },
            grid: {
                top:20,
                left: 20,
                right: 20,
                bottom: 30,
                containLabel: true
            },
            xAxis:  {
                type: 'category',
                data: xAxisData,
                axisLine:{
                    lineStyle:{
                        color: "#379fb7",
                    }
                },
                axisLabel:{
                    color: "#379fb7",
                    interval: 0,
                },
                axisTick: {
                    alignWithLabel: true
                }
            },
            yAxis: {
                type: 'value',
                axisLine:{
                    show:false,
                    lineStyle:{
                        color: "#379fb7",
                    }
                },
                axisLabel:{
                    color: "#379fb7",
                },
                splitLine:{
                    // show:false,
                    lineStyle: {
                        color: "#379fb7",
                        type: "dashed"
                    }
                }
            },
            series: seriesData
        };

        myChart.setOption(option);
    }
    render() {
        return (
            <div id={this.props.id} style={{width:"100%",height:"100%"}}></div>
        )
    }
}

export default class ManagementTeamSituation extends PureComponent {
  render() {
        let datas = [
            { 
                type: "人数",
                data: sexJson.RECORDS,
                chartType: "bar"
            },
            { 
                type: "职称",
                data: zhichengJson.RECORDS,
                chartType: "line"
            },
            { 
                type: "年龄",
                data: ageJson.RECORDS,
                chartType: "bar"
            },
            { 
                type: "学历",
                data: xueliJson.RECORDS,
                chartType: "line"
            },
        ]
        return (
            <Row style={{height:"100%"}}>
                {datas.map((item,i)=>(
                    <Col span={6} style={{height:"100%"}} key={i}>
                        <ItemComp name={item.type} type={item.chartType} id={"ZGDManagementTeamSituationChart"+i} data={item.data}/>
                    </Col>
                ))}
            </Row>
        )
  }
}
