import React, {Component} from 'react';
// import withRouter from 'umi/withRouter';
import echarts from 'echarts';
import { Row, Col } from 'antd';
import jsonData from '../../../data/学校视角--师生比.json';

let myChart;

class BasicSituationStudentTeacherRatio extends Component {
    componentDidMount() {
        myChart = echarts.init(document.getElementById('ZGDBasicSituationStudentTeacherRatio'));

        let chartData = jsonData.RECORDS.map(item=>{
            return {
                name: item.annual,
                studentNum: item.collegeStudentNum,
                teacherNum: item.collegeTeacherNum
            }
        })
        this.setState({
            chartData,
        },()=>{
            this.setChart();
        })
    }

    setChart = () => {
        let data = this.state.chartData;
        if (!data || data.length <= 0) {
            return;
        }
        
        var colors = ['#0181FF', '#44FFFF', '#23FCAF', '#00BFFF'];
        let xAxisData = [], data1 = [], data2 = [];
        data.forEach(item => {
            let rate = Math.round((item.studentNum/item.teacherNum));
            xAxisData.push(item.name+"(1:"+rate+")");
            data1.push(item.teacherNum);
            data2.push(item.studentNum);
        });

        const option = {
            color: colors,
            baseOption: {
                timeline: {
                    show: false,
                    axisType: 'category',
                    currentIndex: 0,
                    data: [],
                },
                grid: [{
                    show: false,
                    left: '10%',
                    top: 10,
                    bottom: 10,
                    containLabel: true,
                    width: '20%',
                }, {
                    show: false,
                    left: '50%',
                    top: 10,
                    bottom: 10,
                    width: '0%',
                }, {
                    show: false,
                    right: '15%',
                    top: 10,
                    bottom: 10,
                    containLabel: true,
                    width: '20%',
                },],

                xAxis: [
                    {
                        type: 'value',
                        inverse: true,
                        axisLine: {
                            show: false,
                        },
                        axisTick: {
                            show: false,
                        },
                        position: 'top',
                        axisLabel: {
                            show: false,
                        },
                        splitLine: {
                            show: false,
                        },
                    }, {
                        gridIndex: 1,
                        show: false,
                    }, {
                        gridIndex: 2,
                        type: 'value',
                        axisLine: {
                            show: false,
                        },
                        axisTick: {
                            show: false,
                        },
                        position: 'top',
                        axisLabel: {
                            show: false,
                        },
                        splitLine: {
                            show: false,
                        },
                    },],
                yAxis: [{
                    type: 'category',
                    inverse: true,
                    position: 'right',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: false,
                        margin: 8,
                        textStyle: {
                            color: '#9D9EA0',
                            fontSize: 14,
                        },

                    },
                    data: xAxisData,
                }, {
                    gridIndex: 1,
                    type: 'category',
                    inverse: true,
                    position: 'left',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#ffffff',
                            fontSize: 14,
                        },

                    },
                    data: xAxisData.map(function (value) {
                        return {
                            value: value,
                            textStyle: {
                                align: 'center',
                            }
                        }
                    }),
                }, {
                    gridIndex: 2,
                    type: 'category',
                    inverse: true,
                    position: 'left',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: false,
                        textStyle: {
                            color: '#9D9EA0',
                            fontSize: 14,
                        },

                    },
                    data: xAxisData,
                },],
                series: [],

            },

            options: [],


        };

        option.baseOption.timeline.data.push(1);
        option.options.push({
            series: [{
                name: '',
                type: 'bar',
                barWidth: '20px',
                label: {
                    show: true,
                    position: 'left',
                    offset: [0, 0],
                    textStyle: {
                        color: '#fff',
                        fontSize: 14,
                    },
                    // formatter: (params)=>{
                    //     if(params.data.value>params.data.value2){
                    //         return `{a|${params.data.value}}`
                    //     } else {
                    //         return `{b|${params.data.value}}`
                    //     }
                    // },
                    rich:{
                        a: {
                            color: 'red',
                            fontSize: 14,
                            fontWeight: "bold"
                        },
                        b: {
                            color: '#7ED321',
                            fontSize: 14,
                            fontWeight: "bold"
                        },
                    }
                },
                itemStyle: {
                    color: '#0181FF',

                },
                data: data1,
            },
            {
                name: '',
                type: 'bar',
                barWidth: '20px',
                xAxisIndex: 2,
                yAxisIndex: 2,
                label: {
                    show: true,
                    position: 'right',
                    offset: [0, 0],
                    textStyle: {
                        color: '#fff',
                        fontSize: 14,
                    },
                    // formatter: (params)=>{
                    //     if(params.data.value>params.data.value2){
                    //         return `{a|${params.data.value}}`
                    //     } else {
                    //         return `{b|${params.data.value}}`
                    //     }
                    // },
                    rich:{
                        a: {
                            color: 'red',
                            fontSize: 14,
                            fontWeight: "bold"
                        },
                        b: {
                            color: '#7ED321',
                            fontSize: 14,
                            fontWeight: "bold"
                        },
                    }
                },
                itemStyle: {
                    color: '#44FFFF',
                },
                data: data2,
            }
            ]
        });

        myChart.setOption(option);
    };

    render() {

        return (
            <div style={{width: '100%', height: '100%'}}>
                <div id="ZGDBasicSituationStudentTeacherRatio" style={{ width: '100%', height: `calc(100% - 30px)` }}></div>
                <Row style={{height:"30px",color:"#41bfc9",fontSize:"14px",textAlign:"center"}}>
                    <Col span={12}>教师</Col>
                    <Col span={12}>学生</Col>
                </Row>
            </div>
        );
    }
}

export default BasicSituationStudentTeacherRatio;