import React, { Component } from 'react'
import { Carousel} from 'antd'
import Xuexiaomianji from './components/BasicSituationSchoolArea'
import Xueshengqingkuang from './components/BasicSituationStudentSituation'
import Tushu from './components/BasicInformationLibraryCollection'
import Shengshibi from './components/BasicSituationStudentTeacherRatio'
import Jiaozhigong from './components/StaffSituation'
import Team from './components/ManagementTeamSituation'
export default class Xuexiao extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>          
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况-学校面积
                            </span>

              </div>
              <div className='item-chart'>
                <Xuexiaomianji></Xuexiaomianji>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况-学生情况
                            </span>

              </div>
              <div className='item-chart'>
              <Xueshengqingkuang></Xueshengqingkuang>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况-图书馆藏
                            </span>

              </div>
              <div className='item-chart'>
              <Tushu></Tushu>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况-生师比
                            </span>

              </div>
              <div className='item-chart'>
              <Shengshibi></Shengshibi>
              </div>              
            </div>
          </div>
          <div className='item-wrapper-4'>
            <div className='item-box'>
                <div className='item-title'>
                  <span>
                  教职工人员情况
                              </span>

                </div>
                <div className='item-chart'>
                  <Jiaozhigong></Jiaozhigong>
                </div>              
              </div>
          </div>
          <div className='item-wrapper-4'>
            <div className='item-box'>
                <div className='item-title'>
                  <span>
                  管理队伍情况
                              </span>

                </div>
                <div className='item-chart'>
                <Team></Team>
                </div>              
              </div>
          </div>
        </div>
        {/* 每一页的划分 */}        
        
      </Carousel>
    )
  }
}
